/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Rubik", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 0 0 10rem;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 3.6rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.4rem;
  color: #FFF;
}

a {
  text-decoration: none;
}

[class$=__heading] {
  margin: 0 0 5rem;
  color: #151F30;
}

.header {
  height: auto;
  margin-top: 5rem;
  margin-bottom: 15rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    height: 20rem;
    margin-bottom: 5rem;
  }
}
.header--registrar {
  flex-direction: column;
  height: auto;
}
.header__logo {
  width: 40rem;
  transition: transform 0.33s;
}
@media (min-width: 768px) {
  .header__logo {
    width: 50rem;
  }
}
@media (min-width: 1024px) {
  .header__logo {
    width: 33rem;
  }
}
.header__logo:hover {
  transform: scale(0.98);
}
.header__imagen {
  width: 100%;
}
@media (min-width: 1024px) {
  .header__imagen {
    width: auto;
  }
}

.registro {
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  align-items: center;
  gap: 10rem;
}
@media (min-width: 1024px) {
  .registro {
    flex-direction: row;
  }
}
.registro__inicioSesion {
  color: #103778;
  font-size: 3.5rem;
  font-weight: 500;
  display: block;
  text-align: center;
  transition: all 0.33s;
}
.registro__inicioSesion:hover {
  color: #E3371E;
  transform: scale(1.1);
}
@media (min-width: 480px) {
  .registro__inicioSesion {
    font-size: 4.5rem;
  }
}
@media (min-width: 1024px) {
  .registro__inicioSesion {
    font-size: 2.5rem;
  }
}
.registro__crearCuenta {
  background-color: #E3371E;
  padding: 2rem 4rem;
  color: #FFF;
  border-radius: 3rem;
  font-size: 3rem;
  font-weight: 700;
  display: block;
  width: auto;
  border: 0;
  cursor: pointer;
  padding: 1rem 2rem;
  font-size: 4.2rem;
  transition: all 0.33s;
}
.registro__crearCuenta:hover {
  background-color: #103778;
  transform: scale(1.1);
}
@media (min-width: 480px) {
  .registro__crearCuenta {
    font-size: 5rem;
  }
}
@media (min-width: 1024px) {
  .registro__crearCuenta {
    font-size: 2.5rem;
  }
}

.formulario__inicio {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5rem;
}
@media (min-width: 1024px) {
  .formulario__inicio {
    flex-direction: row;
    gap: 3rem;
  }
}
.formulario__campo {
  display: grid;
  grid-template-columns: auto;
  justify-items: center;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 1024px) {
  .formulario__campo {
    grid-template-columns: repeat(2, auto);
    gap: 1rem;
  }
}
.formulario__label {
  color: #E3371E;
  font-size: 4rem;
  font-weight: 600;
}
@media (min-width: 1024px) {
  .formulario__label {
    font-size: 2.5rem;
  }
}
.formulario__input {
  height: 3.5rem;
  text-align: center;
  border-radius: 0.5rem;
  border-color: #E3371E;
  border-width: 0.25rem;
  font-size: 2rem;
}
@media (min-width: 1024px) {
  .formulario__input {
    width: 18rem;
    height: 2.5rem;
    font-size: 1.5rem;
    text-align: start;
  }
}
.formulario__boton {
  background-color: #FF7A48;
  padding: 2rem 4rem;
  color: #FFF;
  border-radius: 3rem;
  font-size: 3rem;
  font-weight: 700;
  display: block;
  width: auto;
  border: 0;
  cursor: pointer;
  font-size: 2rem;
  font-weight: 500;
  padding: 1rem;
  transition: all 0.33s;
}
.formulario__boton:hover {
  background-color: #103778;
  transform: scale(0.9) rotate(2deg);
}

.principal {
  margin-bottom: -15rem;
  border-top: 3px solid #103778;
  background-color: #FFF;
  position: relative;
  padding-bottom: 10rem;
  border-bottom-right-radius: 2rem;
  border-bottom-left-radius: 2rem;
}
.principal__heading {
  color: #E3371E;
  font-weight: 700;
  text-align: center;
  margin-top: 5rem;
}
@media (min-width: 480px) {
  .principal__heading {
    font-size: 4.4rem;
  }
}
@media (min-width: 768px) {
  .principal__heading {
    font-size: 7rem;
  }
}
@media (min-width: 1024px) {
  .principal__heading {
    margin-top: 7rem;
  }
}

.cards {
  text-align: center;
  justify-items: center;
}
@media (min-width: 768px) {
  .cards {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .cards {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.card {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card__heading {
  font-weight: 400;
  margin-bottom: 2rem;
}
.card__stock {
  margin: 0 0 2rem;
  font-size: 2.7rem;
  font-weight: 600;
  color: #103778;
}
.card__price {
  color: #151F30;
  font-size: 3.2rem;
  font-weight: 600;
  margin: 0 0 2rem;
}
.card__boton {
  background-color: #E3371E;
  padding: 2rem 4rem;
  color: #FFF;
  border-radius: 3rem;
  font-size: 3rem;
  font-weight: 700;
  display: block;
  width: auto;
  border: 0;
  cursor: pointer;
  transition: all 0.33s;
}
.card__boton:hover {
  background-color: #103778;
  transform: scale(1.1);
  border: 0;
}
.card__boton--volverMenu {
  width: 50%;
  padding: 2rem 4rem;
  font-size: 2.3rem;
}
.card__boton--volverMenu:hover {
  background-color: #0593A2;
}
@media (min-width: 768px) {
  .card__boton--volverMenu {
    padding: 2rem 3rem;
    font-size: 3.5rem;
  }
}
.card__boton-eliminar:hover {
  background-color: #747F7F;
}
.card--coca {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card--coca img {
  max-height: 36.6rem;
  margin: 0 auto;
}

.carrito__heading {
  color: #151F30;
  font-weight: 700;
  text-align: center;
}
@media (min-width: 480px) {
  .carrito__heading {
    font-size: 4rem;
  }
}
@media (min-width: 768px) {
  .carrito__heading {
    font-size: 5.5rem;
  }
}
.carrito__total {
  color: #151F30;
  font-weight: 600;
  font-size: 3.2rem;
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  .carrito__total {
    font-size: 3.9rem;
  }
}
.carrito__boton {
  display: block;
  width: 50%;
  margin: 0 auto 5rem;
  font-size: 2rem;
  padding: 2rem 1rem;
}
@media (min-width: 768px) {
  .carrito__boton {
    font-size: 3rem;
    width: 35%;
    padding: 2rem 4rem;
  }
}
.carrito__boton:hover {
  background-color: #151F30;
}
.carrito__finalizar {
  display: flex;
  flex-direction: column;
  flex-basis: 1;
  justify-content: space-between;
  align-items: center;
  gap: 5rem;
}
@media (min-width: 768px) {
  .carrito__finalizar {
    flex-direction: row-reverse;
  }
}
.carrito__comprar {
  padding: 2rem 6rem;
  font-size: 3.6rem;
  display: block;
  margin: 0 auto;
}
.carrito__comprar:hover {
  background-color: #0593A2;
}
@media (min-width: 768px) {
  .carrito__comprar {
    padding: 2rem 8rem;
    font-size: 5rem;
    margin: 0;
  }
}
.carrito__menuPrincipal {
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
  justify-items: center;
}

.mostrar {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(3, 1fr);
  margin: 0 0 2rem;
}
.mostrar__texto, .mostrar__cantidad, .mostrar__total {
  color: #151F30;
  font-size: 3.2rem;
  font-weight: 600;
  margin: 0;
}
.mostrar__cantidad {
  margin: 0 0 2rem;
}
.mostrar__mas, .mostrar__menos {
  background-color: #FF7A48;
  color: #FFF;
  border: 0;
  padding: 0.5rem;
  border-radius: 2rem;
  display: block;
  font-size: 4rem;
  font-weight: 800;
  width: auto;
  cursor: pointer;
  transition-property: background-color transform;
  transition-duration: 0.3s;
}
.mostrar__mas:hover, .mostrar__menos:hover {
  background-color: #0593A2;
  transform: scale(1.1) rotate(3deg);
}
.mostrar__menos:hover {
  background-color: #151F30;
}

.registrar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: -15rem;
  border-radius: 2rem;
  background-color: #FFF;
  position: relative;
}
.registrar__header {
  margin-top: 0;
  margin-bottom: 10rem;
}
@media (min-width: 1024px) {
  .registrar__header {
    margin-bottom: 5rem;
  }
}
.registrar__volver {
  font-size: 5rem;
}
.registrar__volver:hover {
  color: #151F30;
}
.registrar__heading {
  color: #E3371E;
}
@media (min-width: 480px) {
  .registrar__heading {
    font-size: 5rem;
  }
}
.registrar__contenedor {
  margin-bottom: 3.8rem;
}
.registrar__formulario {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
.registrar__campo {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
}
.registrar__campo--submit {
  margin: 0 auto;
}
.registrar__label {
  font-size: 3rem;
  color: #151F30;
}
.registrar__input {
  text-align: center;
  border-radius: 0.5rem;
  border-color: #E3371E;
  border-width: 0.25rem;
  font-size: 2rem;
}
@media (min-width: 1024px) {
  .registrar__input {
    font-size: 1.9rem;
    text-align: start;
  }
}
.registrar__input--submit {
  background-color: #E3371E;
  padding: 2rem 4rem;
  color: #FFF;
  border-radius: 3rem;
  font-size: 3rem;
  font-weight: 700;
  display: block;
  width: auto;
  border: 0;
  cursor: pointer;
  padding: 1rem 3rem;
  transition: all 0.33s;
}
.registrar__input--submit:hover {
  transform: scale(1.1) rotate(-2deg);
}

.footer {
  background-color: #E3371E;
  height: 20rem;
  position: static;
}
.footer__grid {
  display: grid;
  grid-template-columns: 1fr 3fr;
  gap: 2rem;
  height: 100%;
  align-items: end;
}
.footer__flex {
  display: flex;
  align-items: center;
  gap: 2rem;
  height: 5rem;
}
.footer__flex:nth-child(2) {
  justify-content: flex-end;
}
.footer__img {
  max-width: 3rem;
  min-width: 2rem;
}
@media (min-width: 768px) {
  .footer__img {
    max-width: 3.3rem;
  }
}
.footer__terminos, .footer__about {
  font-weight: 300;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .footer__terminos, .footer__about {
    font-size: 2.4rem;
  }
}